
.box{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 5px 0;
  height: 460px;
  overflow: hidden;
}

.squere{
  width: 450px;
  margin: 200px auto;
  box-sizing: border-box;
  background: #efefef;
  box-shadow: -6px -6px 14px rgba(255, 255, 255, .7),
              -6px -6px 10px rgba(255, 255, 255, .5),
              6px 6px 8px rgba(255, 255, 255, .075),
              6px 6px 10px rgba(0, 0, 0, .15);
}

.item{
  width: 100px;
  height: 100px;
  background: #efefef;
  box-shadow: -6px -6px 14px rgba(255, 255, 255, .7),
              -6px -6px 10px rgba(255, 255, 255, .5),
              6px 6px 8px rgba(255, 255, 255, .075),
              6px 6px 10px rgba(0, 0, 0, .15);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 36px;
  margin:  5px;
}

.counter, .best_score{
  width: 225px;
  height: 50px;
  background: #bdbcbc;
  box-shadow: -6px -6px 14px rgba(255, 255, 255, .7),
              -6px -6px 10px rgba(255, 255, 255, .5),
              6px 6px 8px rgba(255, 255, 255, .075),
              6px 6px 10px rgba(0, 0, 0, .15);
  color: #fff;
  font-weight: 500;
  display: flex;
  justify-content: center;
  align-items: center;
}

.bg{
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
  color: #cccccc;
  font-size: 15em;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.bg p{
  margin: 0 200px;
}

@keyframes anim {
  from{
    opacity: 0;
  }
  to{opacity: 1;}
}

.info{
  position: absolute;
  top: 100px;
  right: 50%;
  transform: translateX(50%);
  display: flex;
  justify-content: space-between;
  gap: 10px
}

.popup_wrapper{
  background-color: rgba(53, 53, 53, 0.329);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup{
  background-color: #e0e0e0;
  width: 350px;
  height: 200px;
  text-align: center;
  padding: 10px;
}

.popup button{
  cursor: pointer;
  height: 50px;
  width: 150px;
  font-size: 24px;
  font-weight: 600;
  margin: 0 5px;
  flex: 1 1 auto;
  border: none;
}


.i-enter {
  opacity: 0;
  transform: scaleX(0%);
}
.i-enter-active {
  opacity: 1;
  transform: scaleX(100%);
  transition: all 500ms ease-in;
}







